@import './vars';

nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    background-color: $white;
    // border-bottom: solid 2px $color1;

    h1 {
        color: $color2;
    }


    ul {
        display: flex;
        align-items: center;

        li {
            margin: 0 10px;

            a {
                color: $color2;
            }

            #login {
                background-color: $color3;
                color: $white;
                padding: 5px 15px;
                border: solid 2px $color3;
                border-radius: 4px;
                transition: all 0.3s ease-out;


                &:hover {
                    background-color: $color2;
                    border: solid 2px $color2;
                    color: $white;
                    transition: all 0.3s ease-out;
                    transition: 250ms;
                }
            }

            #sign-up {
                border: solid 2px $color2;
                padding: 5px 15px;
                border-radius: 4px;
                color: $color2;

                &:hover {
                    background-color: $color2;
                    color: $white;
                    transition: all 0.3s ease-out;
                    transition: 250ms;
                }
            }
        }
    }
}

#sub-nav {
    background: $color2;
    justify-content: center;

    ul {
        margin: auto;
        li {
            padding: 0 5px;

            a {
                color: $white;
                &:hover {
                    text-decoration: underline;
                }
            }
        }
    }
}